import React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    infoTab: {
        justifyContent: 'center',
        alignItems: 'center',
    },
    column: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'left',
        marginTop: 50
    },
    h: {
        fontFamily: 'Comfortaa, sans-serif',
    },
    text: {
        fontFamily: 'Comfortaa, sans-serif',
        textIndent: '30px'
    },
}));

const Summary = () => {
    const classes = useStyles();

    return (
        <Grid container className={classes.infoTab}>
            <Grid item md={10} xs={10} className={classes.column}>
                <h1 className={classes.h}>
                    Streszczenie pracy dyplomowej
                </h1>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        W niniejszej pracy inynierskiej skoncentrowano si na kompleksowym podejciu do zagadnienia ogniw fotowoltaicznych, ze szczeglnym uwzgldnieniem mechanizmu pozycjonowania paneli fotowoltaicznych. Wprowadzenie zawiera okrelenie celu i zakresu pracy, wskazujc na kluczowe zagadnienia zwizane z konwersj energii sonecznej na energi elektryczn.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        W drugim rozdziale omwiono zasady dziaania ogniw fotowoltaicznych, skupiajc si na zjawisku fotowoltaicznym. Przeanalizowano rol materiaw pprzewodnikowych, zjawisko konwersji wiata na energi elektryczn, oraz przedstawiono podstawowe rodzaje ogniw fotowoltaicznych. Rozwaono rwnie czynniki wpywajce na sprawno ogniwa, takie jak parametry zewntrzne, waciwoci materiaowe i straty wewntrzne.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        Trzeci rozdzia powicono mechanizmowi pozycjonowania paneli fotowoltaicznych. Klasyfikacja, budowa i zasada dziaania tego mechanizmu zostay przedstawione w kontekcie wpywu na wydajno ogniw. W szczeglnoci omwiono ram podtrzymujc panele, o obracajc, silnik i ukad napdowy, czujniki wiata oraz sterownik. Przedstawiono take rne algorytmy sterowania, takie jak zegarowe, czujnikowe i hybrydowe.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        W czci sprztowej systemu badawczego (rozdzia czwarty), wskazano kluczowe elementy, takie jak mikrokontroler Raspberry Pi 4 B, sensory pomiarowe, moduy mikrokontrolera, napd sterujcy oraz panele fotowoltaiczne. Opisano zastosowanie kadego z tych elementw w kontekcie zdalnego monitorowania i sterowania pozycjonowaniem ogniw fotowoltaicznych.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        Rozdzia pity powicono oprogramowaniu mikrokontrolera, ze szczeglnym naciskiem na skuteczne sterowanie i monitorowanie pozycjonowania paneli fotowoltaicznych. Oprogramowanie to odgrywa kluczow rol w precyzyjnej kontroli nad mechanizmem ledzcym oraz w zbieraniu danych pomiarowych. Szczegowo omwiono interfejsy, protokoy komunikacyjne i algorytmy uywane do sterowania i monitorowania.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        Kolejny rozdzia skupi si na zdalnym zbieraniu danych pomiarowych, przedstawiajc koncepcj i elementy systemu umoliwiajcego skuteczne monitorowanie i analiz parametrw pracy ogniw fotowoltaicznych. W tym kontekcie zaprezentowano rwnie aplikacj webow, ktra bdzie oparta na technologii React, umoliwiajc atwy dostp i zarzdzanie danymi.
                    </p>
                </Grid>
                <Grid item md={12} xs={12}>
                    <p className={classes.text}>
                        W ostatnim rozdziale przedstawiono caociowe podsumowanie pracy, podkrelajc kluczowe wyniki, wnioski i potencjalne obszary dalszych bada. Praca stanowi kompleksowe spojrzenie na zagadnienie ogniw fotowoltaicznych, integrujc aspekty teoretyczne, praktyczne i technologiczne.
                    </p>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default Summary;
