import React from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(() => ({
    infoTab: {
        justifyContent: 'center',
        alignItems: 'center',
    },
    column: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'left',
        marginTop: 50,
        marginBottom: 50,
        fontFamily: 'Comfortaa, sans-serif',
    },
    text: {
        textIndent: '30px'
    },
    p: {
        marginBottom: '20px'
    }
}));

const InfoTab = () => {
    const classes = useStyles();

    return (
        <Grid container className={classes.infoTab}>
            <Grid item md={10} xs={10} className={classes.column}>
                <Grid
                    item
                    className={classes.p}
                >
                    <h2>
                        Cel Projektu:
                    </h2>
                    <p className={classes.text}>
                        Optymalizacja wydajnoci ogniw fotowoltaicznych poprzez zdalne monitorowanie i sterowanie ich pozycjonowaniem.
                    </p>
                </Grid>
                <Grid
                    item
                    className={classes.p}
                >
                    <h2>
                        Technologie Wykorzystane w Projekcie:
                    </h2>
                    <ul>
                        <li>
                            Mikrokomputer Raspberry Pi 4 B jako centralna jednostka kontrolna.

                        </li>
                        <li>
                            Czujniki natenia wiata TSL25911 do pomiaru warunkw owietleniowych.

                        </li>
                        <li>
                            Moduy mikrokontrolera, takie jak Power Monitoring HAT i Servo PWM Pi Zero PCA9685, do efektywnego zarzdzania pozycjonowaniem.

                        </li>
                        <li>
                            Serwo MG-90S do sterowania ruchem paneli fotowoltaicznych.

                        </li>
                        <li>
                            Ogniwa soneczne 1W/6V 136x110x3mm AKU-03694 jako rdo energii.

                        </li>
                    </ul>
                </Grid>
                <Grid
                    item
                    className={classes.p}
                >
                    <h2>
                        Funkcje Kluczowe:
                    </h2>
                    <ul>
                        <li>
                            Zdalne zbieranie danych pomiarowych dotyczcych natenia wiata.
                        </li>
                        <li>
                            Automatyczne dostosowywanie pozycji paneli fotowoltaicznych w oparciu o warunki owietleniowe.
                        </li>
                        <li>
                            Wykorzystanie algorytmw sterowania do minimalizacji zuycia energii.
                        </li>
                        <li>
                            Moliwo zdalnej konfiguracji i monitorowania systemu poprzez aplikacj webow.
                        </li>
                    </ul>
                </Grid>
                <Grid
                    item
                    className={classes.p}
                >
                    <h2>Korzyci:</h2>
                    <ul>
                        <li>
                            Zwikszenie efektywnoci ogniw fotowoltaicznych.
                        </li>
                        <li>
                            Minimalizacja wpywu zmian warunkw atmosferycznych na wydajno systemu.
                        </li>
                        <li>
                            atwe zarzdzanie i monitorowanie dziki aplikacji webowej.
                        </li>
                        <li>
                            Potencjalne oszczdnoci energetyczne dziki inteligentnemu pozycjonowaniu.
                        </li>
                    </ul>
                </Grid>
                <Grid
                    item
                    className={classes.p}
                >
                    <h2>
                        Oczekiwane Wyniki:
                    </h2>
                    <ul>
                        <li>
                            Zdolno systemu do utrzymania optymalnej orientacji paneli, co przekada si na zwikszon produkcj energii.
                        </li>
                        <li>
                            Dostarczenie narzdzia badawczego dla dalszych analiz dotyczcych wpywu pozycjonowania na wydajno.
                        </li>
                    </ul>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default InfoTab;
